nav {
    background-color: rgba(32, 25, 8, 0.658);
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
  }
  
  nav ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
  }
  
  nav li {
    margin: 0 20px;
  }
  
  nav a {
    color: rgb(243, 235, 235);
    text-decoration: none;
  }
  